.bg-theme {
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.75), 0 0 10px rgba(255, 255, 255, 0.75); }

.fixed-nav {
  position: fixed;
  z-index: 1;
  top: 0.25rem;
  text-transform: uppercase; }
  .fixed-nav + div {
    margin-top: calc(0.25rem + 33px); }
  @media (min-width: 576px) {
    .fixed-nav {
      max-width: 540px; } }
  @media (min-width: 768px) {
