.divide-line-v > *,
.br-img, .divide-line-h > *,
.bb-img, .divide-line-v-m > *,
.br-img-m, .divide-line-h-xl > * {
  position: relative; }

.divide-line-v > *::before,
.br-img::before, .divide-line-h > *::after,
.bb-img::after, .divide-line-v-m > *::before,
.br-img-m::before, .divide-line-h-xl > *::after {
